import {Button, Card, CardActionArea, CardHeader, Container, Paper, Typography} from "@material-ui/core";
import {useHistory} from "react-router-dom";
import {Add, AddBox, AddOutlined} from "@material-ui/icons";

const filters = [
    {
        id: 1,
        title: 'Next PI',
        items: 169
    },
    {
        id: 2,
        title: 'Current PI',
        items: 420
    },
    {
        id: 3,
        title: 'Current iteration',
        items: 2137
    }
]

export const YourFilters = () => {
    const history = useHistory()

    return <Container>
        <Typography variant={'h5'} align={'center'} style={{marginBottom: '16px'}}>
            Your filters
        </Typography>
        {
            filters.map(filter => (<Card elevation={8} style={{marginBottom: 8}} key={filter.title}>
                <CardActionArea onClick={() => history.push(`./filter/${filter.id}`)}>
                    <CardHeader title={filter.title} subheader={`${filter.items} items`}/>
                </CardActionArea>
            </Card>))
        }
        <Button startIcon={<AddBox />} onClick={() => history.push('/create-filter')}>
            Add new filter
        </Button>
    </Container>
}